import { useState } from 'react';
import { FlatList, ScrollView, StatusBar, StyleSheet, Text, TextInput, TouchableOpacity, View } from 'react-native';
import stfx from "./stfx.json"

export default function App() {

  const raw = stfx;

  const [name,setname] = useState("");
  const [srted,setsrted] = useState(raw)
  const [dept,setdept] = useState("All")

  function sort(name,dept) {
      let dat = [...srted];

      if(name==""||name==undefined){ dat = raw;console.log("empty")}

      let sort = [];

      if(dept=="All"){
        dat.map((val,index)=>{
          if(val['Full Name'].includes(name.toUpperCase())){
            sort.push(val);
          }
        })
      }else{
        dat.map((val,index)=>{
          if(val['Full Name'].includes(name.toUpperCase()) && val["Dept"]==dept){
            sort.push(val);
          }
        })
      }
      

      setsrted(sort);
  }


  function Each ({item}) {
    const year = 0;


    return(
      <View style={styles.eachcont}>



        <View style={{width:'35%'}}>
        <Text style={{fontSize:9,textAlign:'center',fontWeight:'bold'}}>{item['Full Name']}</Text>
        </View>

        <View style={{width:'40%'}}>    
        <Text style={{fontSize:8,fontWeight:'600'}}>{item["Email"]}</Text>
            </View>


            <View style={{width:'19%'}}>  
            <Text style={{fontSize:10,fontWeight:'600'}}>{item["Mobile"]}</Text>  
</View>

<View style={{width:'5%'}}>  
<Text style={{fontSize:8,fontWeight:'600'}}>{item["Semester"]}</Text>
</View>

      </View>
    )
  }

  function Btn (props) {

    return(
      <TouchableOpacity style={[{marginBottom:10,marginHorizontal:5,width:100,height:50,alignItems:'center',justifyContent:'center',borderRadius:10},dept==props.name?{backgroundColor:'green',justifyContent:"space-evenly"}:{backgroundColor:'red'}]}
    onPress={()=>{setdept(props.name);sort(name,props.name)}}>
      <Text style={{fontWeight:'bold',color:'white'}}>{props.name}</Text>
      {
        dept==props.name?(<Text style={{fontSize:10}}>{srted.length}</Text>):(null)
      }
      </TouchableOpacity>
    )
  }
  
  

  return (
    <View style={styles.container}>
      <TextInput placeholder='Enter Name' style={styles.txtinput} onChangeText={text=>{setname(text);console.log(text.toUpperCase());sort(text,dept)}} />
      <View style={{width:'100%',flexDirection:'row',justifyContent:'space-evenly',marginTop:5,flexWrap:'wrap'}}>
      <Btn name="All" />
        <Btn name="AI" />
        <Btn name="IT" />
        <Btn name="CS" />
        <Btn name="ECE" />
        <Btn name="EEE" />
        <Btn name="CIVIL" />
        <Btn name="MECH" />

      </View>
        <FlatList 
        data={srted}
        renderItem={Each}
        />



    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    marginTop:StatusBar.currentHeight
  },
  txtinput:{
    width:'90%',
    height:50,
    borderWidth:2,
    borderRadius:10,
    paddingLeft:10,
    alignSelf:'center',
    marginTop:10
  },
  eachcont:{
    width:'99%',
    height:30,
    backgroundColor:'white',
    marginVertical:5,
    flexDirection:'row',
    justifyContent:'space-evenly',
    borderWidth:1,
    borderRadius:10,
    alignItems:'center',
    alignSelf:'center'
  }
});
